module.exports = "import Container from 'react-bootstrap/Container';\nimport Row from 'react-bootstrap/Row';\nimport Col from 'react-bootstrap/Col';\n\nfunction ResponsiveAutoExample() {\n  return (\n    <Container>\n      <Row>\n        <Col sm={8}>sm=8</Col>\n        <Col sm={4}>sm=4</Col>\n      </Row>\n      <Row>\n        <Col sm>sm=true</Col>\n        <Col sm>sm=true</Col>\n        <Col sm>sm=true</Col>\n      </Row>\n    </Container>\n  );\n}\n\nexport default ResponsiveAutoExample;\n"